@import url(https://fonts.googleapis.com/css?family=Lato:300,400|Libre+Baskerville:400,700);
:root {
  --font-base: 1rem;
  --unit: 2px;
  --font-heading-base: calc(var(--font-base) * 2 - var(--unit));
  --font-heading-regular: calc(
    var(--font-heading-base) - var(--font-base) * 0.25
  );
  --font-heading-medium: calc(
    var(--font-heading-base) - var(--font-base) * 0.75
  );
  --font-regular: calc(var(--font-base) - var(--font-base) * 0.125);
  --font-medium: calc(var(--font-base) - var(--unit) - var(--unit));
  --font-small: calc(
    var(--font-base) - var(--unit) - var(--unit) - var(--unit)
  );
  --space-small: 0.25rem;
  --space-medium: 0.5rem;
  --space-regular: 1rem;
  --space-big: 2rem;
  --space-large: 3rem;
  --color-primary: #2a2a2a;
  --color-primary-light: #767676;
  --font-family-headings: "Libre Baskerville", Palatino, Times;
  --font-family-normal: Lato, Helvetica;
}

html {
  font-size: 16px;
}

html,
body {
  font-family: Lato, Helvetica;
  font-family: var(--font-family-normal);
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1,
h2,
h3 {
  font-weight: normal;
  margin: 0.5rem 0;
  margin: var(--space-medium) 0;
  font-family: "Libre Baskerville", Palatino, Times;
  font-family: var(--font-family-headings);
}

h1 {
  font-size: calc(1rem * 2 - 2px);
  font-size: var(--font-heading-base);
}

.App {
  text-align: center;
  padding: 48px 103px 48px 103px;
}

.App ul {
  list-style: none;
}

.horizontal-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 0;
}

@media screen and (max-width: 480px) {
  .App {
    padding: 0 8px;
  }
}

.padding-vertical-regular {
  padding-top: 1rem;
  padding-top: var(--space-regular);
  padding-bottom: 1rem;
  padding-bottom: var(--space-regular);
}

.margin-bottom-regular {
  margin-bottom: 1rem;
  margin-bottom: var(--space-regular);
}

.margin-bottom-big {
  margin-bottom: 2rem;
  margin-bottom: var(--space-big);
}

.margin-bottom-large {
  margin-bottom: 3rem;
  margin-bottom: var(--space-large);
}

.margin-right-regular {
  margin-right: 1rem;
  margin-right: var(--space-regular);
}

.margin-right-big {
  margin-right: 2rem;
  margin-right: var(--space-big);
}

.float-right {
  float: right;
}

.translate-y-50 {
  -webkit-transform: translateY(50%);
  transform: translateY(50%);
}

.designer-portrait {
  width: 100%;
  border-radius: var(--space-small);
  margin-bottom: var(--space-medium);
  -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
  -webkit-filter: grayscale(1);
          filter: grayscale(1);
}

.designer-name {
  text-align: left;
  font-weight: 700;
  font-family: var(--font-family-headings);
  font-size: var(--font-heading-regular);
}

.designer-title {
  text-align: left;
  font-weight: 100;
  font-size: var(--font-heading-medium);
  font-family: var(--font-family-normal);
}

.designer-bio {
  text-align: left;
  font-size: var(--font-regular);
  margin-bottom: 24px;
  letter-spacing: 0.1px;
  line-height: 1.64;
}

.designer-link {
  font-size: var(--font-regular);
}

.designer-info {
  text-align: left;
}

.designer {
  padding: var(--space-small);
}

@media screen and (min-width: 480px) {
  .designer {
    max-width: 260px;
  }
}

.tag input {
  visibility: hidden;
  position: absolute;
}

.tag-content {
  padding: var(--space-small);
  z-index: 2;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.tag-content:hover {
  cursor: pointer;
}

.tag {
  color: var(--color-primary-light);
  display: -webkit-flex;
  display: flex;
  position: relative;
  font-size: var(--font-heading-medium);
  font-weight: normal;
  margin: 0 var(--space-regular);
  z-index: 3;
}

.tag canvas {
  z-index: 1;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
  opacity: 0;
}

.tag--selected {
  color: var(--color-primary);
  font-weight: bold;
}

.tag--selected canvas {
  -webkit-transition: opacity 0.1s;
  transition: opacity 0.1s;
  opacity: 1;
}

